<template>
  <el-row class="frame">
    <el-col :span="7"></el-col>
    <el-col :span="10" class="login_frame">
      <el-container>
        <el-header>
          <div class="register_text">
            <h3>註冊會員</h3>
          </div>
        </el-header>
        <el-main>
          <el-row>
            <el-col :span="21">
              <el-form :model="dynamicValidateForm" label-width="100px" ref="dynamicValidateForm" class="email_frame">
                <el-form-item label="信箱" prop="email">
                  <el-input v-model="dynamicValidateForm.email"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col :span="3"></el-col>
          </el-row>
          <el-row>
            <el-col :span="21">
              <el-form :model="dynamicValidateForm" label-width="100px" ref="dynamicValidateForm" class="account_frame">
                <el-form-item label="帳號" prop="account">
                  <el-input v-model="dynamicValidateForm.account"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col :span="3"></el-col>
          </el-row>
          <el-row>
            <el-col :span="21">
              <el-form label-width="100px" class="password_frame">
                <el-form-item label="密碼" prop="pass">
                  <el-input type="password" v-model="dynamicValidateForm.password" autocomplete="off"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col :span="3"></el-col>
          </el-row>
          <el-row>
            <el-col :span="21">
              <el-form label-width="100px" class="check_pass_frame">
                <el-form-item label="名稱" prop="checkPass">
                  <el-input type="password" v-model="dynamicValidateForm.name" autocomplete="off"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col :span="3"></el-col>
          </el-row>
          <el-row class="checkin_frame">
            <el-col :span="4"></el-col>
            <el-col :span="17">
              <el-button class="checkin" type="primary" @click="submitForm(dynamicValidateForm.email, dynamicValidateForm.account, dynamicValidateForm.password, dynamicValidateForm.name)">註冊</el-button>
            </el-col>
            <el-col :span="3"></el-col>
          </el-row>
        </el-main>
        <el-row class="jump_page">
          <el-col :span="7"></el-col>
          <el-col :span="6" class="text">
            <span>已註冊SHOP會員？</span>
          </el-col>
          <el-col :span="4">
            <div class="subject_link">
              <router-link to="/identify/login">登入</router-link>
            </div>
          </el-col>
          <el-col :span="7"></el-col>
        </el-row>
      </el-container>
    </el-col>
    <el-col :span="7"></el-col>
  </el-row>
</template>

<script>
  export default {
    name: 'register',
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('請輸入密碼'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('請再次輸入密碼'));
        } else if (value !== this.ruleForm.pass) {
          callback(new Error('兩次輸入不一致!'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          pass: '',
          checkPass: ''
        },
        rules: {
          pass: [{
            validator: validatePass,
            trigger: 'blur'
          }],
          checkPass: [{
            validator: validatePass2,
            trigger: 'blur'
          }],
        },
        dynamicValidateForm: {
          email:'',
          account:'',
        },
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
    }
  };
</script>

<style scoped lang="scss" src="./register.scss"></style>